@import url(https://fonts.googleapis.com/css2?family=Krona+One&display=swap);
body {
  margin: 0;
  background: rgb(218, 216, 216);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.spin {
  border: 5px solid lightgray;
  border-top: 5px solid rgb(164, 225, 243);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(360deg)
  }
}

.container {
  width: 650px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.card-front {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.card-front img {
  width: 85%;
}

.word-search {
  width: 400px;
  padding: 5px;
}

.card-container {
  width: 300px;
  height: 350px;
  background: #56afb3;
  border-radius: 50px;
  border: 5px solid #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}

.card-back {
  width: 300px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.card-back h2, p {
  color: #fff;
}

.card-back p {
  text-align: center;
  font-size: 20px;
}
